function oddCount(n){
  if (n <= 0){
    return 0
  }
  if (n % 2 === 0){
    return n / 2
  } else {
    return (n - 1) / 2
  }
}

// or

function oddCount(n){
  return n % 2 === 0 ? n / 2 : (n - 1) / 2 
}

